function ImgViewer(props: { src: any; }) {
    const [ visible, setVisible ] = React.useState(false);
  
    return (
      <div>
            {/* <button onClick={() => { setVisible(true); }}>點我看大圖</button> */}
            <button onClick={() => { setVisible(true); }}>
            <img className="originalimg" alt='origina_img' src={props.src} />
            </button>


            <Viewer
                scalable={false}
                noFooter={true}
                noNavbar={true}
                noToolbar={true}
                noImgDetails={true}	
        visible={visible}
        onClose={() => { setVisible(false); } }
        images={[{src: props.src, alt: ''}]}
        />
      </div>
    );
  }

