const thumbItems = (items: any[], [setThumbIndex, setThumbAnimation]: [React.Dispatch<React.SetStateAction<number>>, React.Dispatch<React.SetStateAction<boolean>>]) => {
    return items.map((item, i) => (
        // eslint-disable-next-line no-sequences
        <div className="thumb" onClick={() => (setThumbIndex(i), setThumbAnimation(false))}>
            {item}
            {/* i{i} */}
            {/* <div className="thumbnail" data-value={i+""}>$$<img alt='img' src={images[i].original} />1</div>, */}
        </div>
    ));
};
