const CarouselS = (props: { postsList: any[]; thumbnailList: any[]; originalList: any[]; }) => {
    const [mainIndex, setMainIndex] = useState(0);
    const [mainAnimation, setMainAnimation] = useState(false);
    const [thumbIndex, setThumbIndex] = useState(0);
    const [thumbAnimation, setThumbAnimation] = useState(false);

    // eslint-disable-next-line @typescript-eslint/no-use-before-define
    // const [thumbs] = useState(thumbItems(thumbnail, [setThumbIndex, setThumbAnimation]));
    const [thumbs] = useState(thumbItems(props.thumbnailList, [setThumbIndex, setThumbAnimation]));


    const slideNext = () => {
        if (!thumbAnimation && thumbIndex < thumbs.length - 1) {
            setThumbAnimation(true);
            setThumbIndex(thumbIndex + 1);
        }
    };

    const slidePrev = () => {
        if (!thumbAnimation && thumbIndex > 0) {
            setThumbAnimation(true);
            setThumbIndex(thumbIndex - 1);
        }
    };

    const syncMainBeforeChange = () => {
        setMainAnimation(true);
    };

    const syncMainAfterChange = (e: { type: string; item: React.SetStateAction<number>; }) => {
        setMainAnimation(false);

        if (e.type === 'action') {
            setThumbIndex(e.item);
            setThumbAnimation(false);
        } else {
            setMainIndex(thumbIndex);
        }
    };

    const syncThumbs = (e: { item: React.SetStateAction<number>; }) => {
        setThumbIndex(e.item);
        setThumbAnimation(false);

        if (!mainAnimation) {
            setMainIndex(e.item);
        }
    };

    return (
        <div className="Carousel">

            <div className="thumbs">
                <div className="btn-prev" onClick={slidePrev}>
                    <svg width="15" height="24" viewBox="0 0 15 24" xmlns="http://www.w3.org/2000/svg"><title>Left Arrow</title><path d="M13 2.308l-10 10 10 10" stroke-width="4" stroke="#C3000B" fill="none" fill-rule="evenodd"></path></svg>
                </div>
                <AliceCarousel
                    activeIndex={thumbIndex}
                    autoWidth
                    disableDotsControls
                    disableButtonsControls
                    items={thumbs}
                    mouseTracking={false}
                    onSlideChanged={syncThumbs}
                    touchTracking={!mainAnimation}
                />

                <div className="btn-next" onClick={slideNext}>
                    <svg width="15" height="24" viewBox="0 0 15 24" xmlns="http://www.w3.org/2000/svg"><title>Right Arrow</title><path d="M2 2.308l10 10-10 10" stroke-width="4" stroke="#C3000B" fill="none" fill-rule="evenodd"></path></svg>
                </div>
            </div>
            <div className="category">
                <div className="category-name">讀者投稿</div>
                <div className="line-top"></div>

            </div>
            <div className="post_img">
                <AliceCarousel
                    activeIndex={mainIndex}
                    animationType="fadeout"
                    animationDuration={800}
                    disableDotsControls
                    disableButtonsControls
                    infinite
                    items={props.originalList}
                    mouseTracking={!thumbAnimation}
                    onSlideChange={syncMainBeforeChange}
                    onSlideChanged={syncMainAfterChange}
                    touchTracking={!thumbAnimation}
                />
             
                <div className="directions">
                    <div className="info">
                    <p className="user">讀者投稿 <span className="time">2021.11.24</span></p>
                        <p className="location">📍 台灣,台北</p>
                </div>

                <img src={CaptureLogo} className="Capture-logo" alt="logo" />
                </div>
                <div className="caption">
                {/* {props.postsList[mainIndex].caption?props.postsList[mainIndex].caption:" 連續3年中國電影缺席金馬獎，華語影壇走進一個時代的隘口──面對創作、巿場以及影響力，亟需不同的思維去突圍。金馬58，我們看見世代、巿場的流動，堅守電影不只是抒情工具，更身負時代責任。"}  */}
                {props.postsList[mainIndex].caption?props.postsList[mainIndex].caption:"這個作者沒有添加說明喔！"} 
  
                </div>

            </div>
        </div>
    )
};


const Carousel = () => {
    const [postsList, setPostsList] = useState([]);
    const [thumbItem, setThumbItem] = useState([]);
    const [originalItem, setOriginalItem] = useState([]);
    const [isLoading, setIsLoading] = useState(false);

    useEffect(() => {
        // const url = "https://api.adviceslip.com/advice";
        const url = "https://dia-backend.numbersprotocol.io/api/v2/assets/?limit=10";
        const fetchData = async () => {
            try {
                const response = await fetch(url);
                const json = await response.json();
                console.log(json);
                let dia_postlist = json.results;
                let postsList = dia_postlist.map((index: { caption: any; updated_at: any;asset_file_thumbnail: any; asset_file_url: any; cai_file: any; cai_file_url: any; cid: any; creator: any; creator_name: any; }) => {
                    return {
                        asset_file_thumbnail: index.asset_file_thumbnail,
                        asset_file_url: index.asset_file_url,
                        cai_file: index.cai_file,
                        cai_file_url: index.cai_file_url,
                        cid: index.cid,
                        creator: index.creator,
                        creator_name: index.creator_name,
                        thumbnail: index.asset_file_thumbnail,
                        original: index.cai_file,
                        time: index.updated_at,
                        caption:index.caption
                    }
                })
                console.log(postsList);
                setPostsList(postsList);
                // postsList.map((item: { thumbnail: string | undefined; }, i: string) => {
                //     setThumbItem(oldArray => [...oldArray, <div className="thumbItem" data-value={i + ""}>$$<img className="thumbnail_img" alt='thumbnail_img' src={item.thumbnail} />{i}</div>]);

                // })
                setThumbItem(postsList.map((item: {caption: string | undefined;  thumbnail: string | undefined; }, i: string) => {
                    return <div className="thumbItem" data-value={i + ""}>
                        
                        <div className="thumbnail-caption"> {item.caption?item.caption:"這個投稿作者沒有添加說明喔！"} </div>

                        {/* <div className="thumbnail-caption"> {item.caption?item.caption:"李雪莉／「歹命」其實是一種社會結構──從《廢墟少年》到《度日》看見的新底層生命"} </div> */}
                            <img className="thumbnail_img" alt='thumbnail_img' src={item.thumbnail} />
                    </div> 
                }))
                setOriginalItem(postsList.map((item: { original: string | undefined; }, i: string) => {
                    return <div className="originalItem" data-value={i + ""}>
                        <ImgViewer src={item.original} />
                        {/* <img className="originalimg" alt='origina_img' src={item.original} /> */}
                    </div>
                }))
                setIsLoading(true)
            } catch (error) {
                console.log("error", error);
            }
        };

        fetchData();
    }, []);

    // const  thumbnailList = (postsList: any[]) => {
    //     return postsList.map((item, i) => {
    //         return  <div className="thumbnail" data-value={i+""}>$$<img alt='img' src={item.thumbnail} />1</div>
    //     });
    // };
    return (
        <div>
            {/* { thumbItem} */}
            {/* { thumbItem[0]}
            {originalItem[0]} */}
            {isLoading ? <CarouselS postsList={postsList} thumbnailList={thumbItem} originalList={originalItem} /> : "non"}
        </div>
    );
}


export default Carousel;